import { InjectionToken } from '@angular/core';

export interface IStyles {
    [key: string]: string;
}
export interface IConfig {
    img?: string;
    loaderStyles?: IStyles;
    imgStyles?: IStyles;
    hostStyles?: IStyles;
    // TODO AnimationOptions
    rotate?: {
        delay?: number;
        direction?: 'normal' | 'reverse' | 'alternate' | 'alternate-reverse';
        duration?: number;
        easing?: string;
        endDelay?: number;
        fill?: 'none' | 'forwards' | 'backwards' | 'both' | 'auto';
        id?: string;
        iterationStart?: number;
        iterations?: number;
    };
}

export type optionsConfig = IConfig;

export const NGX_LOADER_INDICATOR_CONFIG: InjectionToken<string> = new InjectionToken('config');
export const NEW_CONFIG: InjectionToken<string> = new InjectionToken('NEW_CONFIG');
export const INITIAL_CONFIG: InjectionToken<IConfig> = new InjectionToken('INITIAL_CONFIG');

export const initialConfig: IConfig = {
    // tslint:disable-next-line:max-line-length
    img: 'data:image/svg+xml;base64,',
    loaderStyles: {
        position: 'absolute',
        left: '0',
        right: '0',
        top: '0',
        bottom: '0',
        background: 'rgba(255, 255, 255, 0.8)',
        'z-index': '50',
        display: 'flex',
        'justify-content': 'center',
        'align-items': 'center',
    },
    imgStyles: {
        width: '30px',
    },
    hostStyles: {
        position: 'relative',
    },
    rotate: {
        iterations: Infinity,
    },
};
